import React, { useState, useMemo } from 'react';
import { Sliders, Baby, MapPin, Home, TrendingUp, TrendingDown, DollarSign, Heart, RotateCcw, Zap, Info, ArrowLeftRight, Sparkles, AlertTriangle } from 'lucide-react';
import { calculateSimulation } from '@/services/calculationService';
import { Analytics } from '@/services/analytics';
import { SimulationInputs, SimulationResult } from '@/types';
import { DEFAULT_INPUTS } from '@/constants';
import { useTranslation } from '@/services/i18n';

interface WhatIfScenario {
  id: string;
  icon: React.ReactNode;
  title: string;
  description: string;
  color: string;
  fields: Array<{
    key: keyof SimulationInputs;
    label: string;
    type: 'slider' | 'select' | 'toggle';
    min?: number;
    max?: number;
    step?: number;
    options?: Array<{ value: any; label: string }>;
  }>;
}

interface WhatIfSimulatorProps {
  baseInputs: SimulationInputs;
  baseResult: SimulationResult;
}

const getScenarios = (t: (key: string) => string): WhatIfScenario[] => [
  {
    id: 'child',
    icon: <Baby size={24} />,
    title: t('whatif.scenario.child'),
    description: t('whatif.scenario.child.desc'),
    color: 'pink',
    fields: [
      { key: 'children', label: t('whatif.field.children'), type: 'slider', min: 0, max: 5, step: 1 },
      { key: 'familyMembers', label: t('whatif.field.familyMembers'), type: 'slider', min: 1, max: 6, step: 1 },
    ],
  },
  {
    id: 'salary',
    icon: <DollarSign size={24} />,
    title: t('whatif.scenario.salary'),
    description: t('whatif.scenario.salary.desc'),
    color: 'emerald',
    fields: [
      { key: 'annualIncomeCHF', label: t('whatif.field.salary'), type: 'slider', min: 40000, max: 250000, step: 5000 },
    ],
  },
  {
    id: 'residence',
    icon: <Home size={24} />,
    title: t('whatif.scenario.residence'),
    description: t('whatif.scenario.residence.desc'),
    color: 'blue',
    fields: [
      { key: 'frontierWorkerType', label: t('whatif.field.frontierType'), type: 'select', options: [
        { value: 'NEW', label: t('whatif.field.frontierNew') },
        { value: 'OLD', label: t('whatif.field.frontierOld') },
      ]},
      { key: 'distanceZone', label: t('whatif.field.distanceZone'), type: 'select', options: [
        { value: 'WITHIN_20KM', label: t('whatif.field.within20km') },
        { value: 'OVER_20KM', label: t('whatif.field.over20km') },
      ]},
    ],
  },
  {
    id: 'marital',
    icon: <Heart size={24} />,
    title: t('whatif.scenario.marital'),
    description: t('whatif.scenario.marital.desc'),
    color: 'rose',
    fields: [
      { key: 'maritalStatus', label: t('whatif.field.maritalStatus'), type: 'select', options: [
        { value: 'SINGLE', label: t('input.single') },
        { value: 'MARRIED', label: t('input.married') },
        { value: 'DIVORCED', label: t('input.divorced') },
        { value: 'WIDOWED', label: t('input.widowed') },
      ]},
      { key: 'spouseWorks', label: t('whatif.field.spouseWorks'), type: 'toggle' },
    ],
  },
  {
    id: 'age',
    icon: <Zap size={24} />,
    title: t('whatif.scenario.age'),
    description: t('whatif.scenario.age.desc'),
    color: 'amber',
    fields: [
      { key: 'age', label: t('input.age'), type: 'slider', min: 18, max: 65, step: 1 },
    ],
  },
];

const colorMap: Record<string, { bg: string; text: string; border: string; light: string }> = {
  pink: { bg: 'bg-pink-500', text: 'text-pink-600', border: 'border-pink-200 dark:border-pink-800', light: 'bg-pink-50 dark:bg-pink-950/30' },
  emerald: { bg: 'bg-emerald-500', text: 'text-emerald-600', border: 'border-emerald-200 dark:border-emerald-800', light: 'bg-emerald-50 dark:bg-emerald-950/30' },
  blue: { bg: 'bg-blue-500', text: 'text-blue-600', border: 'border-blue-200 dark:border-blue-800', light: 'bg-blue-50 dark:bg-blue-950/30' },
  rose: { bg: 'bg-rose-500', text: 'text-rose-600', border: 'border-rose-200 dark:border-rose-800', light: 'bg-rose-50 dark:bg-rose-950/30' },
  amber: { bg: 'bg-amber-500', text: 'text-amber-600', border: 'border-amber-200 dark:border-amber-800', light: 'bg-amber-50 dark:bg-amber-950/30' },
};

const WhatIfSimulator: React.FC<WhatIfSimulatorProps> = ({ baseInputs, baseResult }) => {
  const { t } = useTranslation();
  const [modifiedInputs, setModifiedInputs] = useState<Partial<SimulationInputs>>({});
  const [activeScenario, setActiveScenario] = useState<string>('child');

  const whatIfInputs = useMemo(() => ({
    ...baseInputs,
    ...modifiedInputs,
  }), [baseInputs, modifiedInputs]);

  const whatIfResult = useMemo(() => {
    return calculateSimulation(whatIfInputs as SimulationInputs);
  }, [whatIfInputs]);

  const handleFieldChange = (key: keyof SimulationInputs, value: any) => {
    setModifiedInputs(prev => ({ ...prev, [key]: value }));
    Analytics.trackUIInteraction('simulatore', 'what_if', 'campo_parametro', 'cambio', `${key}:${value}`);
  };

  const handleReset = () => {
    setModifiedInputs({});
    Analytics.trackUIInteraction('simulatore', 'what_if', 'bottone_reset', 'click', activeScenario);
  };

  const handleScenarioChange = (id: string) => {
    setActiveScenario(id);
    setModifiedInputs({});
    Analytics.trackUIInteraction('simulatore', 'what_if', 'selettore_scenario', 'cambio', id);
  };

  // Calculate differences
  const baseNetIT = baseResult.itResident.netIncomeMonthly;
  const baseNetCH = baseResult.chResident.netIncomeMonthly;
  const newNetIT = whatIfResult.itResident.netIncomeMonthly;
  const newNetCH = whatIfResult.chResident.netIncomeMonthly;
  const diffIT = newNetIT - baseNetIT;
  const diffCH = newNetCH - baseNetCH;
  
  // "Savings" is really IT-vs-CH differential. Positive = living in IT is better
  const baseDifferential = baseResult.savingsEUR;
  const newDifferential = whatIfResult.savingsEUR;
  const diffDifferential = newDifferential - baseDifferential;
  
  // Best option for the user
  const bestOptionNow = newDifferential >= 0 ? 'IT' : 'CH';
  const bestNetNow = bestOptionNow === 'IT' ? newNetIT : newNetCH;
  const bestNetBase = bestOptionNow === 'IT' ? baseNetIT : baseNetCH;
  const bestNetDiff = bestNetNow - bestNetBase;

  const hasChanges = Object.keys(modifiedInputs).length > 0;

  const scenarios = getScenarios(t);
  const currentScenario = scenarios.find(s => s.id === activeScenario)!;
  const colors = colorMap[currentScenario.color];

  return (
    <div className="space-y-6 animate-fade-in">
      {/* Header â€” Fun/Playful */}
      <div className="bg-gradient-to-br from-violet-600 via-fuchsia-500 to-pink-500 rounded-3xl p-8 text-white shadow-2xl relative overflow-hidden">
        {/* Decorative blobs */}
        <div className="absolute -top-10 -right-10 w-40 h-40 bg-white/10 rounded-full blur-2xl" />
        <div className="absolute -bottom-10 -left-10 w-32 h-32 bg-yellow-300/15 rounded-full blur-2xl" />
        <div className="relative z-10">
          <div className="flex items-center gap-4 mb-4">
            <div className="p-3 bg-white/20 rounded-2xl backdrop-blur-sm">
              <Sparkles size={32} className="text-yellow-300" />
            </div>
            <div className="flex-1 min-w-0">
              <div className="flex items-center gap-3 flex-wrap">
                <h1 className="text-2xl sm:text-3xl font-extrabold">{t('whatif.title')}</h1>
                <span className="inline-flex items-center gap-1.5 px-3 py-1 text-[10px] font-bold uppercase tracking-wider bg-yellow-400/25 text-yellow-200 border border-yellow-300/40 rounded-full">
                  âœ¨ {t('whatif.experimentalBadge')}
                </span>
              </div>
              <p className="text-white/70 mt-1 text-sm">{t('whatif.subtitle')}</p>
            </div>
          </div>
          {/* Fun hint bar */}
          <div className="mt-4 bg-white/10 rounded-xl p-3 backdrop-blur-sm text-sm text-white/60">
            <div className="flex items-center gap-2">
              <Sliders size={14} className="text-yellow-300" />
              <span>{t('whatif.terminalHint')}</span>
            </div>
          </div>
        </div>
      </div>

      {/* Scenario Tabs */}
      <div className="flex gap-2 overflow-x-auto pb-2">
        {scenarios.map(scenario => {
          const sc = colorMap[scenario.color];
          return (
            <button
              key={scenario.id}
              onClick={() => handleScenarioChange(scenario.id)}
              className={`flex items-center gap-2 px-4 py-3 rounded-xl font-bold text-sm whitespace-nowrap transition-all ${
                activeScenario === scenario.id
                  ? `${sc.bg} text-white shadow-lg scale-105`
                  : 'bg-white dark:bg-slate-800 text-slate-600 dark:text-slate-300 border border-slate-200 dark:border-slate-700 hover:shadow-md'
              }`}
            >
              {scenario.icon}
              <span className="hidden sm:inline">{scenario.title}</span>
            </button>
          );
        })}
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Controls */}
        <div className={`rounded-2xl border-2 ${colors.border} p-6 ${colors.light}`}>
          <div className="flex items-center justify-between mb-4">
            <h3 className="text-lg font-bold text-slate-800 dark:text-slate-100 flex items-center gap-2">
              {currentScenario.icon}
              {currentScenario.title}
            </h3>
            {hasChanges && (
              <button
                onClick={handleReset}
                className="flex items-center gap-1 px-3 py-1.5 text-xs font-bold text-slate-500 hover:text-slate-700 bg-white dark:bg-slate-800 rounded-lg border border-slate-200 dark:border-slate-700 transition-all"
              >
                <RotateCcw size={12} />
                {t('whatif.reset')}
              </button>
            )}
          </div>
          <p className="text-sm text-slate-500 dark:text-slate-500 mb-6">{currentScenario.description}</p>

          <div className="space-y-6">
            {currentScenario.fields.map(field => {
              const currentVal = whatIfInputs[field.key as keyof SimulationInputs];
              const baseVal = baseInputs[field.key as keyof SimulationInputs];
              const isChanged = currentVal !== baseVal;

              return (
                <div key={String(field.key)} className="space-y-2">
                  <div className="flex items-center justify-between">
                    <label className="text-sm font-bold text-slate-700 dark:text-slate-300">
                      {field.label}
                    </label>
                    {isChanged && (
                      <span className="text-xs font-bold text-violet-600 dark:text-violet-400 bg-violet-100 dark:bg-violet-900/30 px-2 py-0.5 rounded-full">
                        {t('whatif.modified')}
                      </span>
                    )}
                  </div>

                  {field.type === 'slider' && (
                    <div className="space-y-2">
                      <input
                        type="range"
                        min={field.min}
                        max={field.max}
                        step={field.step}
                        value={currentVal as number}
                        onChange={(e) => handleFieldChange(field.key, Number(e.target.value))}
                        className="w-full h-2 bg-slate-200 dark:bg-slate-700 rounded-full appearance-none cursor-pointer accent-violet-600"
                      />
                      <div className="flex justify-between text-xs text-slate-500">
                        <span>{field.min?.toLocaleString('it-IT')}</span>
                        <span className={`font-bold text-base ${isChanged ? 'text-violet-600' : 'text-slate-800 dark:text-slate-200'}`}>
                          {(currentVal as number).toLocaleString('it-IT')}
                          {field.key === 'annualIncomeCHF' && ' CHF'}
                        </span>
                        <span>{field.max?.toLocaleString('it-IT')}</span>
                      </div>
                    </div>
                  )}

                  {field.type === 'select' && (
                    <select
                      value={currentVal as string}
                      onChange={(e) => handleFieldChange(field.key, e.target.value)}
                      className="w-full px-4 py-2.5 bg-white dark:bg-slate-900 border border-slate-200 dark:border-slate-700 rounded-xl text-slate-800 dark:text-slate-200 font-semibold focus:outline-none focus:ring-2 focus:ring-violet-500"
                    >
                      {field.options?.map(opt => (
                        <option key={opt.value} value={opt.value}>{opt.label}</option>
                      ))}
                    </select>
                  )}

                  {field.type === 'toggle' && (
                    <button
                      onClick={() => handleFieldChange(field.key, !(currentVal as boolean))}
                      className={`relative w-14 h-7 rounded-full transition-colors ${
                        currentVal ? 'bg-violet-600' : 'bg-slate-300 dark:bg-slate-600'
                      }`}
                    >
                      <div className={`absolute top-0.5 left-0.5 w-6 h-6 bg-white rounded-full shadow transition-transform ${
                        currentVal ? 'translate-x-7' : ''
                      }`} />
                    </button>
                  )}
                </div>
              );
            })}
          </div>
        </div>

        {/* Impact Cards */}
        <div className="lg:col-span-2 space-y-4">
          {/* Difference Summary */}
          <div className={`grid grid-cols-1 sm:grid-cols-3 gap-4 ${!hasChanges ? 'opacity-40' : ''}`}>
            <div className="bg-white dark:bg-slate-800 rounded-2xl border border-slate-200 dark:border-slate-700 p-5 shadow-sm">
              <div className="text-xs font-bold text-slate-500 dark:text-slate-500 uppercase mb-1">
                ðŸ‡®ðŸ‡¹ {t('whatif.netIT')}
              </div>
              <div className="text-2xl font-extrabold text-slate-800 dark:text-slate-100">
                â‚¬ {Math.round(newNetIT).toLocaleString('it-IT')}
              </div>
              {hasChanges && (
                <div className={`flex items-center gap-1 mt-1 text-sm font-bold ${diffIT >= 0 ? 'text-emerald-600' : 'text-red-600'}`}>
                  {diffIT >= 0 ? <TrendingUp size={14} /> : <TrendingDown size={14} />}
                  {diffIT >= 0 ? '+' : ''}{Math.round(diffIT).toLocaleString('it-IT')} â‚¬
                </div>
              )}
            </div>

            <div className="bg-white dark:bg-slate-800 rounded-2xl border border-slate-200 dark:border-slate-700 p-5 shadow-sm">
              <div className="text-xs font-bold text-slate-500 dark:text-slate-500 uppercase mb-1">
                ðŸ‡¨ðŸ‡­ {t('whatif.netCH')}
              </div>
              <div className="text-2xl font-extrabold text-slate-800 dark:text-slate-100">
                CHF {Math.round(newNetCH).toLocaleString('it-IT')}
              </div>
              {hasChanges && (
                <div className={`flex items-center gap-1 mt-1 text-sm font-bold ${diffCH >= 0 ? 'text-emerald-600' : 'text-red-600'}`}>
                  {diffCH >= 0 ? <TrendingUp size={14} /> : <TrendingDown size={14} />}
                  {diffCH >= 0 ? '+' : ''}{Math.round(diffCH).toLocaleString('it-IT')} CHF
                </div>
              )}
            </div>

            <div className={`rounded-2xl border-2 p-5 shadow-sm ${bestOptionNow === 'IT' ? 'bg-emerald-50 dark:bg-emerald-950/30 border-emerald-200 dark:border-emerald-800' : 'bg-blue-50 dark:bg-blue-950/30 border-blue-200 dark:border-blue-800'}`}>
              <div className="text-xs font-bold text-slate-500 dark:text-slate-500 uppercase mb-1 flex items-center gap-1">
                <ArrowLeftRight size={12} />
                {t('whatif.bestOption')}
              </div>
              <div className={`text-lg font-extrabold ${bestOptionNow === 'IT' ? 'text-emerald-600' : 'text-blue-600'}`}>
                {bestOptionNow === 'IT' ? 'ðŸ‡®ðŸ‡¹ ' + t('whatif.liveInItaly') : 'ðŸ‡¨ðŸ‡­ ' + t('whatif.liveInSwiss')}
              </div>
              <div className="text-xs text-slate-500 mt-1">
                {t('whatif.diffPerYear')}: <span className={`font-bold ${newDifferential >= 0 ? 'text-emerald-600' : 'text-blue-600'}`}>
                  â‚¬ {Math.abs(Math.round(newDifferential)).toLocaleString('it-IT')}
                </span>
              </div>
            </div>
          </div>

          {/* Detailed Comparison */}
          {hasChanges && (
            <div className="bg-white dark:bg-slate-800 rounded-2xl border border-slate-200 dark:border-slate-700 p-6 shadow-sm animate-fade-in">
              <h3 className="text-lg font-bold text-slate-800 dark:text-slate-100 mb-4 flex items-center gap-2">
                <Info size={20} className="text-violet-600" />
                {t('whatif.impactDetail')}
              </h3>
              <div className="space-y-3">
                {/* IT Breakdown */}
                <div className="space-y-2">
                  <div className="text-sm font-bold text-slate-600 dark:text-slate-500">ðŸ‡®ðŸ‡¹ {t('whatif.residenceItaly')}</div>
                  <div className="grid grid-cols-3 gap-2">
                    <div className="p-3 bg-slate-50 dark:bg-slate-900 rounded-lg">
                      <div className="text-xs text-slate-500">{t('whatif.grossAnnual')}</div>
                      <div className="font-bold text-slate-800 dark:text-slate-100">
                        â‚¬ {Math.round(whatIfResult.itResident.grossIncome * whatIfResult.exchangeRate).toLocaleString('it-IT')}
                      </div>
                    </div>
                    <div className="p-3 bg-slate-50 dark:bg-slate-900 rounded-lg">
                      <div className="text-xs text-slate-500">{t('whatif.totalTaxes')}</div>
                      <div className="font-bold text-red-600">
                        â‚¬ {Math.round(whatIfResult.itResident.taxes * whatIfResult.exchangeRate).toLocaleString('it-IT')}
                      </div>
                    </div>
                    <div className="p-3 bg-emerald-50 dark:bg-emerald-900/20 rounded-lg">
                      <div className="text-xs text-slate-500">{t('whatif.netMonthly')}</div>
                      <div className="font-bold text-emerald-700 dark:text-emerald-400">
                        â‚¬ {Math.round(newNetIT).toLocaleString('it-IT')}
                      </div>
                      {diffIT !== 0 && (
                        <div className={`text-[10px] font-bold ${diffIT >= 0 ? 'text-emerald-600' : 'text-red-600'}`}>
                          {diffIT >= 0 ? 'â†‘' : 'â†“'} {diffIT >= 0 ? '+' : ''}{Math.round(diffIT)} â‚¬
                        </div>
                      )}
                    </div>
                  </div>
                </div>

                {/* CH Breakdown */}
                <div className="space-y-2">
                  <div className="text-sm font-bold text-slate-600 dark:text-slate-500">ðŸ‡¨ðŸ‡­ {t('whatif.residenceSwiss')}</div>
                  <div className="grid grid-cols-3 gap-2">
                    <div className="p-3 bg-slate-50 dark:bg-slate-900 rounded-lg">
                      <div className="text-xs text-slate-500">{t('whatif.grossAnnual')}</div>
                      <div className="font-bold text-slate-800 dark:text-slate-100">
                        CHF {Math.round(whatIfResult.chResident.grossIncome).toLocaleString('it-IT')}
                      </div>
                    </div>
                    <div className="p-3 bg-slate-50 dark:bg-slate-900 rounded-lg">
                      <div className="text-xs text-slate-500">{t('whatif.totalTaxes')}</div>
                      <div className="font-bold text-red-600">
                        CHF {Math.round(whatIfResult.chResident.taxes).toLocaleString('it-IT')}
                      </div>
                    </div>
                    <div className="p-3 bg-blue-50 dark:bg-blue-900/20 rounded-lg">
                      <div className="text-xs text-slate-500">{t('whatif.netMonthly')}</div>
                      <div className="font-bold text-blue-700 dark:text-blue-400">
                        CHF {Math.round(newNetCH).toLocaleString('it-IT')}
                      </div>
                      {diffCH !== 0 && (
                        <div className={`text-[10px] font-bold ${diffCH >= 0 ? 'text-emerald-600' : 'text-red-600'}`}>
                          {diffCH >= 0 ? 'â†‘' : 'â†“'} {diffCH >= 0 ? '+' : ''}{Math.round(diffCH)} CHF
                        </div>
                      )}
                    </div>
                  </div>
                </div>

                {/* Summary â€” clearer explanation */}
                <div className="border-t border-slate-200 dark:border-slate-700 pt-3">
                  <div className={`p-4 rounded-xl ${bestOptionNow === 'IT' ? 'bg-emerald-50 dark:bg-emerald-900/20' : 'bg-blue-50 dark:bg-blue-900/20'}`}>
                    <div className="text-sm font-bold text-slate-700 dark:text-slate-300 mb-2">
                      ðŸ“Š {t('whatif.scenarioSummary')}
                    </div>
                    
                    {/* Show what actually happens with this scenario change */}
                    <div className="grid grid-cols-2 gap-3 mb-3">
                      <div className="bg-white/70 dark:bg-slate-800/70 rounded-lg p-3">
                        <div className="text-[10px] font-bold text-slate-500 uppercase">{t('whatif.yourNetChange')}</div>
                        <div className={`text-xl font-extrabold ${diffIT >= 0 ? 'text-emerald-600' : 'text-red-600'}`}>
                          {diffIT >= 0 ? '+' : ''}{Math.round(diffIT).toLocaleString('it-IT')} â‚¬/{t('common.months')}
                        </div>
                        <div className="text-[10px] text-slate-500">
                          = {diffIT >= 0 ? '+' : ''}{Math.round(diffIT * 12).toLocaleString('it-IT')} â‚¬/{t('whatif.perYear')}
                        </div>
                        <div className="text-[10px] text-slate-500 mt-0.5">({t('whatif.asItalyFrontier')})</div>
                      </div>
                      <div className="bg-white/70 dark:bg-slate-800/70 rounded-lg p-3">
                        <div className="text-[10px] font-bold text-slate-500 uppercase">{t('whatif.chComparison')}</div>
                        <div className={`text-xl font-extrabold ${diffCH >= 0 ? 'text-emerald-600' : 'text-red-600'}`}>
                          {diffCH >= 0 ? '+' : ''}{Math.round(diffCH).toLocaleString('it-IT')} CHF/{t('common.months')}
                        </div>
                        <div className="text-[10px] text-slate-500">
                          = {diffCH >= 0 ? '+' : ''}{Math.round(diffCH * 12).toLocaleString('it-IT')} CHF/{t('whatif.perYear')}
                        </div>
                        <div className="text-[10px] text-slate-500 mt-0.5">({t('whatif.asSwissResident')})</div>
                      </div>
                    </div>

                    {/* IT vs CH advantage explanation */}
                    <div className="bg-white/50 dark:bg-slate-800/50 rounded-lg p-3 border border-slate-200/50 dark:border-slate-700/50">
                      <div className="flex items-center gap-2 text-sm">
                        <ArrowLeftRight size={14} className="text-violet-600" />
                        <span className="text-slate-600 dark:text-slate-500">{t('whatif.comparisonLabel')}:</span>
                      </div>
                      <div className={`text-lg font-extrabold mt-1 ${newDifferential >= 0 ? 'text-emerald-600' : 'text-blue-600'}`}>
                        {newDifferential >= 0 
                          ? `ðŸ‡®ðŸ‡¹ ${t('whatif.italyBetterBy')} â‚¬ ${Math.abs(Math.round(newDifferential)).toLocaleString('it-IT')}/${t('whatif.perYear')}`
                          : `ðŸ‡¨ðŸ‡­ ${t('whatif.swissBetterBy')} â‚¬ ${Math.abs(Math.round(newDifferential)).toLocaleString('it-IT')}/${t('whatif.perYear')}`
                        }
                      </div>
                      {hasChanges && diffDifferential !== 0 && (
                        <div className="text-xs text-slate-500 mt-1">
                          {t('whatif.vsCurrentScenario')}: <span className={`font-bold ${diffDifferential >= 0 ? 'text-emerald-600' : 'text-red-600'}`}>
                            {diffDifferential >= 0 ? '+' : ''}{Math.round(diffDifferential).toLocaleString('it-IT')} â‚¬
                          </span>
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )}

          {!hasChanges && (
            <div className="bg-gradient-to-br from-violet-50 to-pink-50 dark:from-violet-950/30 dark:to-pink-950/30 rounded-2xl border-2 border-dashed border-violet-300/60 dark:border-violet-600/40 p-8 text-center">
              <div className="relative inline-block">
                <Sparkles size={48} className="text-violet-400 mx-auto mb-4" />
              </div>
              <p className="text-lg font-bold text-slate-700 dark:text-slate-300">{t('whatif.modifyParams')}</p>
              <p className="text-sm text-violet-500/70 dark:text-violet-400/60 mt-1">{t('whatif.resultsRealtime')}</p>
              <p className="text-[10px] text-slate-500 dark:text-slate-500 mt-3 italic">{t('whatif.nerdDisclaimer')}</p>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default WhatIfSimulator;
